import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import DataService from '../../services/data.service';
import AddressModal from "./addressModal";

export const ChangeAddressModal = (props) => {
    const [address, setAddress] = useState([]);

    const handleRadioChange = (data,index) => {
        props.data(data,index)
    }

    useEffect(() => {
        getAddress();
        // eslint-disable-next-line
    }, [address.length]);

    const getAddress = () => {
        DataService.getAllAddress()
            .then((data) => {
                setAddress(data.data.message)
            })
        props.data((address.length > 0 ? address[0] : ""),0)
    }

    return (
        <>
            <div className="modal fade" id="changeAddress" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog">
                    <div className="modal-content p-4">
                        <div className="modal-header border-0">
                            <h4 className="modal-title" id="exampleModalLabel">Change address</h4>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        {
                            address.length > 0 ?
                                address.map((item, i) =>
                                    <>
                                        <div className="card mb-3">
                                            <div className="card-body d-flex flex-wrap">
                                                {
                                                    i === 0 ?
                                                        <input defaultChecked class="form-check-input radio-position cus-radio" name="address" type="radio"
                                                            onClick={() => handleRadioChange(item,i)} />
                                                        :
                                                        <input class="form-check-input radio-position cus-radio" name="address" type="radio"
                                                            onClick={() => handleRadioChange(item,i)} />
                                                }
                                                <div className="w-100 ms-5">
                                                    <h5 className="f-17"><span className="tag-text ms-2 text-uppercase">Home</span></h5>
                                                    <p className="f-15 w-100 mt-4">{item.house_no}, {item.area}, {item.city}<br/> {item.state} - {item.pincode}<br />
                                                    </p>
                                                    <p className="w-100 mt-2"><button className="btn btn-outline-dark text-uppercase me-3 radius-3 f-500">Remove</button><button className="btn btn-outline-dark text-uppercase radius-3 f-500">Edit</button></p>
                                                </div>
                                            </div>
                                        </div>
                                    </>
                                )
                                :
                                "Address not added yet."
                        }
                        <div className="card mb-3">
                            <div className="card-body px-xs-0 d-flex justify-content-between align-items-center">
                                <h6 className="mb-0">Add New Address</h6>
                                <button className="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#addAddress">ADD ADDRESS</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <AddressModal />
        </>
    )
}
