import React, { useState } from "react";
import RenderSmoothImage from "./RenderSmoothImage";
import "../core/styles.css";
import { useEffect } from "react";
import DataService from "../services/data.service";
import { Link } from "react-router-dom";

const Category = (props) => {
  console.log(props)
  const [categoryProducts, setCategoryProducts] = useState([]);
  const [loading,setLoading] = useState(true);

  useEffect(() => {
    getCategories();
    // eslint-disable-next-line
  }, [props.start, props.page_length])

  const getCategories = () => {
    DataService.getCategory(
      props.item_group ? props.item_group : "Men",
      props.start ? props.start : 0,
      props.page_length ? props.page_length : 10
    ).then((data) => {
      //console.log(data)
      setCategoryProducts(data.data.message.products)
      setLoading(false);
    })
  }

  return (
    <>
      <div className="container-fluid text-center no-padding black-bg">
        <div className="col-lg-6 m-auto">
          {loading && (
            <span className="spinner-border spinner-border-sm"></span>
          )}
        </div>
      </div>
      <section className="browse-cat u-line section-padding">
        <div className="container">
          <div className="row">
            {
              categoryProducts.length > 0 ?
                categoryProducts.map((item, i) => (
                  <div key={i} className="col-12 col-md-4 mt-3">
                    <div className="p-5 prod-box">
                      <Link to={"/detail/" + item.id + '#' + item.description.split('-')[1]}>
                        <RenderSmoothImage
                          src={item.image}
                          alt={item.title}
                          className="img-fluid"
                        />
                      </Link>
                      <h2 className="h5 text-center mt-3 mb-3">{item.title} </h2>
                      <p className="text-center">
                        {item.desc} <br></br>
                        <a href="/" className="shop-now">
                          Shop Now
                        </a>
                      </p>
                    </div>
                  </div>
                ))
                :
                <p>Nothing to show here yet</p>
            }
          </div>
        </div>
      </section>
    </>
  );
};

export default Category;
