import React, { Fragment } from "react";
import { useState } from "react";
import Category from "../../core/Category";

const Content = (props) => {
  const [start, setStart] = useState(0);
  const [pageLength, setPagelength] = useState(10);

  return (
    <Fragment>
      <div className="container-fluid text-center no-padding black-bg">
        <div className="col-lg-6 m-auto">
          <div>
            <h3 className="h3">Category {props.group}</h3>
          </div>
          <div className="input-container">
            <div>
              <b>Start : </b><input className="input-start"
                type="number" value={start}
                onChange={e => setStart(e.target.value)}
                min={0}
              />
            </div>
            <div>
              <b>Page Length : </b><input className="input-pageLength"
                type="number" value={pageLength}
                onChange={e => setPagelength(e.target.value)}
                min={1}
              />
            </div>
          </div>
        </div>
      </div>
      <section className="container py-5">
        <div className="row">
          <Category item_group={props.group} start={start} page_length={pageLength} />
        </div>
      </section>
    </Fragment>
  );

}

export default Content;
