import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Homepage from "./pages/Homepage";
import Bestsellerpage from "./pages/Bestsellerpage";
import Login from "./pages/Login";
import PageNotFound from "./pages/PageNotFound";
import DetailPage from "./pages/productDetail";
import MyBag from "./pages/myBag";
import Address from "./pages/address";
import { RestrictedAccess } from "./private component/RestrictedAccess";
import Register from "./pages/Register";
import CategoryPage from "./pages/CategoryPage";
import { Profile } from "./pages/Profile";
import { Payment } from "./pages/Payment";

export default function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Homepage />}>
          <Route index element={<Homepage />} />
        </Route>
        <Route exact path="/Bestseller" element={<Bestsellerpage />} />
        <Route exact path="/category/:item_group" element={<CategoryPage/>}></Route>
        <Route exact path="/detail/:item_id" element={<DetailPage />} />
        <Route exact path="/Login" element={<Login />} />
        <Route exact path="/register" element={<Register />} />
        <Route path="*" element={<PageNotFound />} />
        <Route element={<RestrictedAccess/>}>
          <Route exact path="/mybag" element={<MyBag />} />
          <Route exact path="/address" element={<Address />} />
          <Route exact path="/profile" element={<Profile/>}></Route>
          <Route exact path="/payment" element={<Payment/>}/>
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
