import React, { Fragment, useRef } from "react";
import { useState } from "react";
import DataService from "../../services/data.service";

const AddressModal = (props) => {
  const form = useRef();
  const [pincode, setPincode] = useState("");
  const [house_no, setHouse_no] = useState("");
  const [area, setArea] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [loading, setLoading] = useState(false);

  const addAddress = (e) => {
    e.preventDefault();
    setLoading(pincode, house_no, area, city, state);
    DataService.addNewAddress(pincode, house_no, area, city, state)
      .then((data) => {
        console.log(data)
        if (data.data.message.success_key === 1) {
          window.location.reload(false)
        }
        if (data.data.message.success_key === 0) {
          setLoading(false);
        }
      },
        (error) => {
          console.log(error)
          if (error.response.message.success_key === 0) {
            setLoading(false);
          }
        }
      )
  }

  return (
    <Fragment>
      <div class="modal fade" id="addAddress" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content p-4">
            <div class="modal-header border-0">
              <h4 class="modal-title" id="exampleModalLabel">Add new address</h4>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <form onSubmit={addAddress} ref={form}>
                <div class="mb-3">
                  <input type="text" required class="form-control" placeholder="Your Name" />
                </div>
                <div class="mb-3">
                  <input type="number" required class="form-control" placeholder="Your Mobile Number" />
                </div>
                <div class="mb-3">
                  <input type="number" class="form-control" placeholder="Pincode"
                    value={pincode} onChange={e => setPincode(e.target.value)} />
                </div>
                <div class="mb-3">
                  <input type="text" class="form-control" placeholder="Address ( House/Flat/Street)"
                    value={house_no} onChange={e => setHouse_no(e.target.value)} />
                </div>
                <div class="mb-3">
                  <input type="text" class="form-control" placeholder="Locality/Town"
                    value={area} onChange={e => setArea(e.target.value)} />
                </div>
                <div class="mb-3">
                  <input type="text" class="form-control" placeholder="City/District"
                    value={city} onChange={e => setCity(e.target.value)} />
                </div>
                <div class="mb-3">
                  <input type="text" class="form-control" placeholder="State"
                    value={state} onChange={e => setState(e.target.value)} />
                </div>
                <ul className="address-place mb-3">
                  <li>
                    <input type="radio" class="btn-check" name="options-outlined" id="success-outlined" autocomplete="off" checked />
                    <label class="btn btn-outline-dark" for="success-outlined">Home</label>
                  </li>
                  <li>
                    <input type="radio" class="btn-check" name="options-outlined" id="danger-outlined" autocomplete="off" />
                    <label class="btn btn-outline-dark" for="danger-outlined">Work</label>
                  </li>
                </ul>
                <button type="submit" className="btn btn-primary w-100 bg-dark-black" disabled={loading}>
                  {loading && (
                    <span className="spinner-border spinner-border-sm"></span>
                  )}
                  <span>SAVE ADDRESS</span>
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default AddressModal;