import React, { Fragment } from "react";
import Pricedetail from "../productprice/pricedetail";
import { ChangeAddressModal } from "../address/changeAddressModal";
import { useEffect } from "react";
import DataService from "../../services/data.service";
import { useState } from "react";
import AddressModal from "../address/addressModal";

const Content = () => {
  const [wishlist, setWishlist] = useState([]);
  const [address, setAddress] = useState([]);
  const [loading, setLoading] = useState(true);
  const [checked, setChecked] = useState([]);
  const [allChecked, setAllChecked] = useState(true);
  const [selectedCount, setSelectedCount] = useState(0);

  const [selectedAddress, setSelectedAddress] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(0);

  const handleCheckboxChange = (i) => {
    const updatedCheckState = checked.map((item, index) =>
      i === index ? !item : item
    );
    setChecked(updatedCheckState);

    const count = updatedCheckState.reduce(
      (sum, currentState) => {
        if (currentState === true) {
          return sum + 1;
        }
        return sum;
      },
      0
    );
    setSelectedCount(count)
    if (count < wishlist.length) {
      setAllChecked(false)
    }
    if (count === wishlist.length) {
      setAllChecked(true)
    }
  }

  const handleAllCheckedChange = () => {
    setAllChecked(!allChecked)
    let updatedCheckState;
    if (allChecked) {
      updatedCheckState = new Array(wishlist.length).fill(false)
      setChecked(updatedCheckState)
    }
    else {
      updatedCheckState = new Array(wishlist.length).fill(true)
      setChecked(updatedCheckState)
    }
    const count = updatedCheckState.reduce(
      (sum, currentState) => {
        if (currentState === true) {
          return sum + 1;
        }
        return sum;
      },
      0
    );
    setSelectedCount(count)
    if (count === 0) {
      setAllChecked(false)
    }
    if (count === wishlist.length) {
      setAllChecked(true)
    }
  }

  const selectAddress = (address, index) => {
    if (address.pincode && address.area && address.house_no && address.city && address.state) {
      setSelectedAddress(address)
    }
    setSelectedIndex(index)
  }

  useEffect(() => {
    getWishlist();
    getAddress();
    // eslint-disable-next-line
  }, [wishlist.length])

  const getWishlist = () => {
    DataService.wishlist_checkOld()
      .then((data) => {
        //console.log(data)
        setWishlist(data.data.message)
        setChecked(new Array(wishlist.length).fill(true))
        setSelectedCount(wishlist.length)
        setLoading(false)
      })
  }

  const getAddress = () => {
    DataService.getAllAddress()
      .then((data) => {
        //console.log(data)
        setAddress(data.data.message)
      })
  }

  return (
    <Fragment>
      <div id="checkoutUi" className="container max-1200 min-height">
        <div className="row">
          <div className="col my-5">
            <ul className="checkoutsteps">
              <li className="active steps">BAG</li>
              <li className="steps">ADDRESS</li>
              <li className="steps">PAYMENT </li>
            </ul>
          </div>
          <div className="col-lg-8">
            <div className="card mb-3">
              {
                address.length > 0 ?
                  <div class="addressStrip-base-desktopContainer">
                    <div class="addressStrip-base-title">
                      <div class="addressStrip-base-addressName">
                        <b>Deliver to:</b>
                        <div className="addressStrip-base-highlight"> {selectedAddress.pincode}</div>
                      </div>
                      <div className="addressStrip-base-subText">
                        {selectedAddress.house_no},{' '}
                        {selectedAddress.area},{' '}
                        {selectedAddress.city},{' '}
                        {selectedAddress.state}
                      </div>
                    </div>
                    <button className="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#changeAddress">CHANGE ADDRESS</button>
                  </div>
                  :
                  <div className="card-body px-xs-0 d-flex justify-content-between align-items-center">
                    <h6 className="mb-0">Add your home address</h6>
                    <button className="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#addAddress">ADD ADDRESS</button>
                  </div>
              }
            </div>
            {/*<div className="card border-start-0 border-top-0 border-end-0 rounded-0 pb-4 ">
              <div className="card-header border-0 p-0 no-bg"><h5>DELIVERY SERVICE </h5></div>
              <div className="card-body px-xs-0 d-flex justify-content-between align-items-center">
                <input type="text" className="pincode" placeholder="Enter Pincode" />
                <button className="btn btn-secondary">CHECK PIN CODE</button>
              </div>
            </div>*/}
            <div className="card border-0">
              <div className="card-body d-flex justify-content-between align-items-center py-4 my-1">
                <p className="m-0 f-20 f-500 customCheck">
                  <input class="form-check-input" type="checkbox"
                    checked={allChecked} onChange={handleAllCheckedChange}
                  /> {selectedCount}/{wishlist.length} Items Selected
                </p>
                <span className="itemsAction">
                  <a href="#0" className="text-danger f-16 f-500 no-decoration">Remove</a>
                  <a href="#0" className="f-16 f-500 no-decoration gray3">Move To Wishlist</a>
                </span>
              </div>
            </div>
            <div className="container-fluid text-center no-padding">
              <div className="col-lg-6 m-auto">
                {loading && (
                  <span className="spinner-border spinner-border-sm"></span>
                )}
              </div>
            </div>
            {
              wishlist.length > 0 ?
                wishlist.map((item, i) =>
                  <div className="card mb-3">
                    <div className="card-body d-flex flex-wrap">
                      <div className="product-thumb">
                        <input class="form-check-input" type="checkbox" checked={checked[i]} onChange={() => handleCheckboxChange(i)} />
                        <img class="img-fluid" src="" alt="product" />
                      </div>
                      <div className="product-details-checkout">
                        <h5>{item.item_code}</h5>
                        <p>Men Black VENTURE RUNNER Sneakers</p>
                        <p>Sold by: </p>
                        <div className="dimensions">
                          <div className="selectDim me-2">
                            Size: {item.item_code.split('-')[1]}
                          </div>
                          <div className="selectDim">
                            Qty: 1
                          </div>
                        </div>
                        <h4 className="mt-3 d-flex align-items-center f-16">Rs. {item.discounted_price} <span className="oldPrice line-through f-14">Rs. {item.price}</span><span className="discountPrice f-16">(Rs. {item.discount_amount} OFF)</span></h4>

                        <button className="closePosition"><i><svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M18.5 1.5L1.5 18" stroke="black" stroke-width="1.5" stroke-linecap="round" />
                          <path d="M1 1.5L19 18" stroke="black" stroke-width="1.5" stroke-linecap="round" />
                        </svg></i></button>
                      </div>
                    </div>
                  </div>
                )
                :
                <div className="container-fluid text-center no-padding">
                  <div className="col-lg-6 m-auto">
                    Add products in wishlist
                  </div>
                </div>
            }
          </div>
          <div className="col-lg-4">
            <Pricedetail wishlist={wishlist.length > 0 ? wishlist : ""} index={selectedIndex} />
          </div>
        </div>
      </div>
      <ChangeAddressModal data={selectAddress} />
      <AddressModal />
    </Fragment>
  );
};

export default Content;
