import React from 'react'
import Footer from '../core/Footer';
import Navbar from '../core/Navbar';
import { Content } from '../sections/payment/Content';

export const Payment = () => {
    React.useEffect(() => {
        document.title = "Exclusive Elements - Payment";
    }, []);

    return (
        <div className="app">
            <Navbar/>
            <Content/>
            <Footer/>
        </div>
    );
}
