import React from "react";
import RenderSmoothImage from "./RenderSmoothImage";
import "../core/styles.css";
import { Link } from "react-router-dom";

const BestSeller = (bestselling) => {
  return (
    <section className="u-line section-padding">
      <div className="container">
        <div className="row">
          {bestselling?.bestselling?.length > 0?
          bestselling.bestselling.map((item, i) => (
            <div key={i} className="col-12 col-md-4 mt-3">
              <div className=" p-5 prod-box">
                <Link to={"/detail/" + item.id+'#'+item.description.split('-')[1] }>
                  <RenderSmoothImage
                    src={item.image}
                    alt={item.item_name}
                    className="img-fluid"
                  />
                </Link>
                <h2 className="h5 text-center mt-3 mb-3">{item.title}</h2>
                <p className="text-center">
                  {item.description} <br></br>
                  <span className="price">{item.price}</span>
                </p>
              </div>
            </div>
          )):""}
        </div>
      </div>
    </section>
  );
};
export default BestSeller;
